import React from 'react';
import { Dropdown, DropdownItem, KebabToggle } from '@patternfly/react-core';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';

class ClusterListExtraActions extends React.Component {
  constructor(props) {
    super(props);
    this.onToggle = (isOpen) => {
      this.setState({
        isOpen,
      });
    };
    this.onSelect = () => {
      this.setState(state => ({
        isOpen: !state.isOpen,
      }));
    };
  }

  state = {
    isOpen: false,
  }

  render() {
    const { isOpen } = this.state;
    const { className, aiEnabled } = this.props;
    const dropdownItems = [
      <DropdownItem component="button" key="registercluster" data-testid="register-cluster-item">
        <div>
          <Link to="/register" className="pf-c-dropdown__menu-item">
            Register cluster
          </Link>
        </div>
      </DropdownItem>,
      <DropdownItem component="button" key="archived">
        <div>
          <Link to="archived" className="pf-c-dropdown__menu-item">
            View cluster archives
          </Link>
        </div>
      </DropdownItem>,
    ];
    if (aiEnabled) {
      dropdownItems.push(
        <DropdownItem component="button" key="assistedinstaller">
          <div>
            <Link to="/assisted-installer" className="pf-c-dropdown__menu-item">
              Assisted Installer clusters
            </Link>
          </div>
        </DropdownItem>,
      );
    }
    return (
      <Dropdown
        data-testid="cluster-list-extra-actions-dropdown"
        onSelect={this.onSelect}
        toggle={<KebabToggle onToggle={this.onToggle} />}
        isOpen={isOpen}
        isPlain
        dropdownItems={dropdownItems}
        className={className}
      />
    );
  }
}

ClusterListExtraActions.propTypes = {
  currentFlags: PropTypes.shape({
    showArchived: PropTypes.bool.isRequired,
  }).isRequired,
  className: PropTypes.string,
  aiEnabled: PropTypes.bool,
};

export default ClusterListExtraActions;
